.hourly-section {
    background-color: var(--hourly-bg-color);
    border-radius: .6rem;
    font-size: .875rem;
    max-width: 625px;
    overflow-x: auto;
    padding: 0 1rem 1rem;
}

.hourly-title {
    padding: 1rem 0;
    font-weight: 500;
    text-transform: uppercase;
    color: var(--hourly-text-color);
}

.hourly-extra-info-wrapper {
    display: grid;
    grid-template-columns: repeat(auto-fit, 125px);
    grid-auto-flow: column;
    grid-auto-columns: 125px;
    gap: .5rem;
}

.hourly-extra-info {
    display: grid;
    grid-auto-rows: 1fr auto 40px;
    gap: 1rem;
    border-radius: .6rem;
    /* background-color: rgb(255, 190, 121); */
    background-color: var(--hourly-card-color);
    color: var(--hourly-card-text-color);
}

.hourly-extra-info-primary {
    display: grid;
    gap: .5rem;
    padding: 1rem;
}

.hourly-extra-info-secondary {
    display: grid;
    gap: .25rem;
    padding: 1rem;
}

.hourly-temp {
    font-weight: 600;
    font-size: 1rem;
}

.hourly-time {
    text-align: center;
    padding: .5rem;
    border-top: 1px solid var(--hourly-bd-color);
}

.hourly-wind,
.hourly-humidity {
    display: flex;
    gap: .25rem;
    height: 1rem;
    align-items: center;
}

@media (min-width: 700px) {
    .hourly-section {
        grid-column: 1 / 3;
        grid-row: 2 / 3;
        max-width: 1000px;
        overflow-x: auto;
    }
}

.hourly-temp-description {
    text-transform: capitalize;
}

.hourly-weather-icon {
    width: 2.5rem;
    width: 2.5rem;
}