.today-section {
    display: grid;
    padding: 1rem;
    background-color: var(--today-bg-color);
    border-radius: .6rem;
    font-size: .875rem;
    gap: 1rem;
    overflow-x: auto;
    max-width: 625px;
    min-height: 263px;
    color: var(--today-text-color);
}

.today-heading-title {
    font-size: 1rem;
    text-transform: uppercase;
    font-weight: 600;
}

.today-main-details {
    display: flex;
    gap: 2rem;
    /* background-color: rgb(146, 190, 255); */
    background-color: var(--today-weather-bg-color);
    color: var(--today-weather-text-color);
    padding: .5rem;
    border-radius: .6rem;
}

.today-main-temp {
    display: flex;
    gap: .5rem;
}

.today-weather-icon {
    width: 3rem;
    height: 3rem;
}

.today-main-temp__details {
    font-size: 3rem;
    display: flex;
}

.today-temp-unit {
    display: flex;
    font-size: 2rem;
    padding-top: .5rem;
}

.today-main-description {
    text-transform: uppercase;
    margin-top: .5rem;
}

.today-main-description-heading {
    font-weight: 600;
    text-transform: capitalize;
}

.today-main-temp-value {
    font-weight: 600;
}

.today-main-summary {
    font-weight: 500;
}

.today-extra-info-wrapper {
    display: grid;
    grid-auto-columns: 1fr;
    grid-auto-flow: column;

    gap: 1rem;
}

.today-extra-info {
    background-color: var(--toady-extra-info-color);
    color: var(--today-extra-info-text-color);
    min-width: 75px;
    display: inline-flex;
    flex-direction: column;
    border-radius: .6rem;
    padding: .25rem;
}

.today-extra-info-title {
    text-transform: uppercase;
}

.today-extra-info-value {
    font-weight: 600;
}

.main-des {
    text-transform: lowercase;
}